.redeem-vote-pop,
.vote-pop {
  .modal-content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 60px;
    padding-bottom: 85px;

    .content-icon {
      background-size: 80px 80px;
      width: 80px;
      height: 80px;
    }
    .content-title {
      margin-top: 20px;

      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      text-align: center;

      color: #ffffff;
      .vote-title {
        font-weight: 600;
        font-size: 20px;
        line-height: 27px;
      }
    }
    .content-subtitle {
      margin-top: 10px;

      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      text-align: center;

      color: rgba(255, 255, 255, 0.6);
    }
    .bold {
      font-weight: 600;
    }

    .input-container {
      margin-top: 50px;

      width: 100%;

      .input-header {
        display: flex;
        margin-bottom: 10px;

        .input-title {
          flex-grow: 1;
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;

          color: rgba(255, 255, 255, 0.6);
        }
        .remaining-votes {
          display: flex;
          .text {
            font-weight: 400;
            font-size: 12px;
            line-height: 20px;

            color: rgba(255, 255, 255, 0.6);
          }
          .balance {
            margin-left: 6px;
            font-weight: 500;
            font-size: 12px;
            line-height: 20px;

            color: #ffffff;
          }
        }
      }
      .j-input {
        margin-bottom: 0;
      }
      .j-error-tip {
        position: relative;
        top: 0;
        margin-top: 6px;
        opacity: 0;
        min-height: 40px;
        transition: all 0.3s ease-in-out;

        &.show-error {
          opacity: 1;
        }
      }
    }

    &.have-input {
      padding-bottom: 22px;
    }
  }

  .j-large-btn {
    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 0;
    background: #4c54ff;
    &:hover {
      background: #9195fb;
    }
    &:focus,
    &:active {
      background: #4c54ff;
    }
    &:disabled,
    &:disabled:hover {
      color: rgba(255, 255, 255, 0.6);
      background: #53545a;
      cursor: not-allowed;
    }
  }
}

.redeem-vote-pop {
  .modal-content-container {
    .content-icon {
      background: url(../../images/v2/vote-detail/popup-no-vote.svg) 0 0 no-repeat;
    }
  }
}
.vote-pop {
  .modal-content-container {
    .content-icon {
      background: url(../../images/v2/vote-detail/popup-vote.svg) 0 0 no-repeat;

      &.no-vote {
        background: url(../../images/v2/vote-detail/popup-no-vote.svg) 0 0 no-repeat;
      }
      &.transaction-sent {
        background: url(../../images/v2/vote-detail/popup-confirm.svg) 0 0 no-repeat;
      }
    }
  }

  .j-large-btn {
    .siging-icon {
      display: none;
    }
    &.is-signing {
      .siging-icon {
        display: block;
      }
    }
  }
}

@media screen and (min-width: 769px) {
}

@media screen and (max-width: 768px) {
  .redeem-vote-pop,
  .vote-pop {
    .modal-content-container {
      .content-subtitle {
        margin-top: 4px;
      }
      &.have-input {
        padding-bottom: 30px;
      }
    }
  }
}

.j-wrapper.white {
  .redeem-vote-pop,
  .vote-pop {
    .modal-content-container {
      .content-title {
        color: #22232b;
      }
      .content-subtitle {
        color: rgba(34, 35, 43, 0.6);
      }
      .input-container {
        .input-header {
          .input-title {
            color: rgba(34, 35, 43, 0.6);
          }
          .remaining-votes {
            .text {
              color: rgba(34, 35, 43, 0.6);
            }
            .balance {
              color: #22232b;
            }
          }
        }
      }
    }
    .j-large-btn {
      &:disabled,
      &:disabled:hover {
        color: rgba(255, 255, 255, 0.6);
        background: #d3d3d5;
      }
    }
  }

  .redeem-vote-pop {
    .modal-content-container {
      .content-icon {
        background: url(../../images/v2/vote-detail/popup-no-vote-white-theme.svg) 0 0 no-repeat;
      }
    }
  }
  .vote-pop {
    .modal-content-container {
      .content-icon {
        background: url(../../images/v2/vote-detail/popup-vote-white-theme.svg) 0 0 no-repeat;

        &.no-vote {
          background: url(../../images/v2/vote-detail/popup-no-vote-white-theme.svg) 0 0 no-repeat;
        }
        &.transaction-sent {
          background: url(../../images/v2/vote-detail/popup-confirm-white-theme.svg) 0 0 no-repeat;
        }
      }
    }
  }
}
