.vote-detail {
  color: #ffffff;

  .content-container {
    background: #282931;
    border-radius: 2px;
  }
  .content-container-shadow {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  }
  .back-to-vote-button {
    position: relative;
    display: block;
    height: 20px;

    padding-left: 24px;
    margin-bottom: 14px;

    font-weight: 500;
    font-size: 15px;

    color: #ffffff;
    opacity: 0.6;
    transition: none;

    &::after {
      content: '';
      background: url(../../images/v2/vote-detail/arrow-left-white.svg) 0 0 no-repeat;
      width: 14px;
      height: 9px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &:hover {
      color: #9195fb;
      opacity: 1;

      &::after {
        background: url(../../images/v2/vote-detail/arrow-left-purple.svg) 0 0 no-repeat;
      }
    }
  }

  .vote-detail-header {
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;

    .vote-detail-title-container {
      flex-grow: 1;
      display: flex;
      flex-direction: column;

      .vote-detail-title {
        margin-top: 24px;
        margin-bottom: 12px;

        font-weight: 500;
        font-size: 24px;
        line-height: 34px;

        .proposal-id-tag {
          margin-left: 10px;
          padding: 4px 10px;

          font-weight: 500;
          font-size: 12px;
          line-height: 16px;

          color: rgba(255, 255, 255, 0.6);

          vertical-align: middle;
          background: rgba(255, 255, 255, 0.04);
          border-radius: 2px;
        }
      }
      .vote-detail-links-row {
        display: flex;
        margin-bottom: 30px;
        .link-item {
          position: relative;
          padding-right: 18px;

          font-weight: 400;
          font-size: 14px;
          line-height: 20px;

          color: #ffffff;
          opacity: 0.6;
          transition: all 0.3s;

          &:not(:first-child) {
            margin-left: 30px;
          }

          &::before {
            content: '';
            background: rgba(255, 255, 255, 0.16);
            border-radius: 2px;
            width: 12px;
            height: 12px;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
          }
          &::after {
            content: '';
            background: url(../../images/v2/vote-detail/new-tab-icon-white.svg) 0 0 no-repeat;
            width: 7px;
            height: 8px;
            position: absolute;
            top: 50%;
            right: 2px;
            transform: translateY(-50%);
          }

          &:hover {
            opacity: 1;
          }
        }
      }
    }
    .vote-detail-status-box {
      padding: 4px 20px 22px;

      .status-history {
        position: relative;
        margin-top: 18px;
        height: 16px;
        display: flex;

        .timeline-icon {
          position: relative;
          width: 16px;
          height: 16px;
          margin-right: 6px;

          &::after {
            content: '';
            position: absolute;
            width: 16px;
            height: 16px;
            top: -1px;
            background: url(../../images/v2/vote-detail/status-succeeded.svg) 0 0 no-repeat;
          }
          &.succeeded::after {
            background: url(../../images/v2/vote-detail/status-succeeded.svg) 0 0 no-repeat;
          }
          &.failed::after {
            background: url(../../images/v2/vote-detail/status-failed.svg) 0 0 no-repeat;
          }
          &.pending::after {
            background: url(../../images/v2/vote-detail/status-pending.svg) 0 0 no-repeat;
          }
          &.waiting::after {
            background: url(../../images/v2/vote-detail/status-waiting.svg) 0 0 no-repeat;
          }
        }
        &:not(:first-child)::before {
          content: '';
          position: absolute;
          width: 2px;
          height: 10px;
          top: -15px;
          left: 7px;
          background: url(../../images/v2/vote-detail/timeline-dot.png) 0 0 no-repeat;
          background-size: 2px 10px;
        }
        .status-title {
          flex-grow: 1;

          font-weight: 400;
          font-size: 12px;
          line-height: 16px;
        }
        .status-time {
          margin-left: 6px;

          font-weight: 400;
          font-size: 12px;
          line-height: 16px;

          color: rgba(255, 255, 255, 0.6);
        }
      }
    }
  }

  $progress-bar-border-radius: 4px;
  .progress-bar-container {
    display: flex;
    flex-direction: row;

    margin-bottom: 20px;

    .support-progress-bar-container,
    .against-progress-bar-container {
      height: 100px;
      min-width: 300px;
      flex-grow: 1;
      padding-bottom: 14px;

      position: relative;
      display: flex;
      align-items: center;

      background-color: #282931;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      border-radius: $progress-bar-border-radius;

      .progress-bar-title,
      .progress-bar-vote-string,
      .wallet-vote-container,
      .action-button {
        position: relative;
        z-index: 1;
      }
      .progress-bar-title {
        font-weight: 500;
        font-size: 20px;
        line-height: 28px;
      }
      .progress-bar-vote-string {
        flex-grow: 1;

        font-weight: 600;
        font-size: 14px;
        line-height: 19px;

        margin-left: 14px;
      }
      .wallet-vote-container {
        position: relative;
        margin-left: 14px;
        margin-right: 14px;
        padding-left: 28px;

        .vote-icon {
          position: absolute;
          background: url(../../images/v2/vote-detail/vote.svg) 0 0 no-repeat;
          background-size: 18px 20px;
          width: 18px;
          height: 20px;
          left: 0;
        }
        .wallet-vote-count {
          font-weight: 600;
          font-size: 14px;
          line-height: 19px;
        }
      }
      .action-button {
        padding: 6px 12px;
        color: #ffffff;
        min-width: 90px;
        border-radius: 2px;

        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-transform: capitalize;
        text-align: center;

        &:disabled {
          color: rgba(255, 255, 255, 0.6);
          background: #53545a;
          cursor: not-allowed;
        }
      }

      .progress-bar {
        position: absolute;
        bottom: 0;

        height: 14px;
        width: 100%;

        border-radius: $progress-bar-border-radius;

        &.square-upper-half {
          border-radius: 0;
          height: 7px;
          bottom: 7px;
        }
      }
      .progress-bar-background {
        position: absolute;
        z-index: 0;
        top: 0;
        width: 100px;
        height: 100%;

        background-color: #282931;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
        border: none;
        border-radius: $progress-bar-border-radius;
        overflow: hidden;

        .progress-bar {
          border-radius: 0;
        }
      }

      &.zero-vote {
        .progress-bar {
          background: #33343b;
        }
      }
    }

    .support-progress-bar-container {
      margin-right: 40px;
      flex-basis: 100%;

      .progress-bar-title {
        margin-left: 30px;
      }
      .wallet-vote-container {
        .wallet-vote-count {
          color: #18c19f;
        }
      }
      .action-button {
        margin-right: 10px;
        background: #18c19f;
      }

      .progress-bar {
        background: #8ff7e2;
      }
      .progress-bar-background {
        right: 0;

        transform: skew(-22.5deg) translateX(25px);
        clip-path: inset(-50px -50px -50px 0px);
      }
    }
    .against-progress-bar-container {
      margin-left: 40px;
      flex-basis: 100%;

      .progress-bar-title {
        margin-left: 10px;
      }
      .wallet-vote-container {
        .wallet-vote-count {
          color: #ff5266;
        }
      }
      .action-button {
        margin-right: 30px;
        background: #ff5266;
      }

      .progress-bar {
        background: #ff5266;
      }
      .progress-bar-background {
        left: 0;
        top: 0;

        transform: skew(-22.5deg) translateX(-25px);
        clip-path: inset(-50px 0px -50px -50px);
      }
    }
  }

  .connect-wallet-hint-bar,
  .redeem-vote-bar {
    margin-bottom: 20px;
    padding: 20px 30px;
    display: flex;
    align-items: center;

    .bar-title {
      flex-grow: 1;

      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
    }
    .bar-action-btn {
      padding: 2px 20px 0px;

      font-weight: 500;
      font-size: 14px;
      line-height: 24px;

      color: #fff;

      background: #4d54ff;
      border-radius: 2px;
    }
  }

  .vote-detail-info-container {
    margin-top: 10px;

    .vote-detail-info-header {
      padding: 20px 30px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.06);

      font-weight: 500;
      font-size: 20px;
      line-height: 28px;
    }
    .vote-detail-info-text {
      padding: 30px;
    }
  }
}

@media screen and (min-width: 769px) {
  .vote-detail {
    padding-top: 30px;

    .vote-detail-header {
      .vote-detail-title-container {
        flex-grow: 1;
      }
      .vote-detail-status-box {
        margin-left: 30px;
        flex-shrink: 0;
        min-width: 300px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .j-container {
    & > div {
      width: 100%;
    }
  }

  .vote-detail {
    padding: 20px 15px 0;
    .vote-detail-header {
      flex-direction: column;
    }
  }
}

.j-wrapper.white {
  .vote-detail {
    color: #22232b;

    .content-container {
      background: #ffffff;
    }
    .content-container-shadow {
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
    }

    .back-to-vote-button {
      color: #22232b;

      &::after {
        background: url(../../images/v2/vote-detail/arrow-left-grey.svg) 0 0 no-repeat;
      }

      &:hover {
        color: #9195fb;

        &::after {
          background: url(../../images/v2/vote-detail/arrow-left-purple.svg) 0 0 no-repeat;
        }
      }
    }

    .vote-detail-header {
      .vote-detail-title-container {
        .vote-detail-title {
          .proposal-id-tag {
            color: rgba(34, 35, 43, 0.8);
            background: rgba(34, 35, 43, 0.06);
          }
        }
        .vote-detail-links-row {
          .link-item {
            color: #22232b;
            opacity: 0.6;

            &::before {
              background: rgba(34, 35, 43, 0.16);
            }
            &::after {
              background: url(../../images/v2/vote-detail/new-tab-icon-grey.svg) 0 0 no-repeat;
            }

            &:hover {
              opacity: 1;
            }
          }
        }
      }

      .vote-detail-status-box {
        .status-history {
          .timeline-icon {
            &::after {
              background: url(../../images/v2/vote-detail/status-succeeded-white-theme.svg) 0 0 no-repeat;
            }
            &.succeeded::after {
              background: url(../../images/v2/vote-detail/status-succeeded-white-theme.svg) 0 0 no-repeat;
            }
            &.failed::after {
              background: url(../../images/v2/vote-detail/status-failed-white-theme.svg) 0 0 no-repeat;
            }
            &.pending::after {
              background: url(../../images/v2/vote-detail/status-pending-white-theme.svg) 0 0 no-repeat;
            }
            &.waiting::after {
              background: url(../../images/v2/vote-detail/status-waiting-white-theme.svg) 0 0 no-repeat;
            }
          }
          &:not(:first-child)::before {
            background: url(../../images/v2/vote-detail/timeline-dot-white-theme.png) 0 0 no-repeat;
            background-size: 2px 10px;
          }
          .status-time {
            color: rgba(34, 35, 43, 0.6);
          }
        }
      }
    }

    .progress-bar-container {
      .support-progress-bar-container,
      .against-progress-bar-container {
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
        background-color: #ffffff;

        .progress-bar-background {
          box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
          background-color: #ffffff;
        }

        .wallet-vote-container {
          .vote-icon {
            background: url(../../images/v2/vote-detail/vote-white-theme.svg) 0 0 no-repeat;
          }
        }

        &.zero-vote {
          .progress-bar {
            background: #f6f6f7;
          }
        }
      }
      .support-progress-bar-container {
        .progress-bar {
          background: #18c19f;
        }
      }
    }

    .vote-detail-info-container {
      .vote-detail-info-header {
        border-bottom: 1px solid rgba(34, 35, 43, 0.1);
      }
    }
  }
}
